* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", sans-serif;
}

.home {
  overflow: hidden;
}

img {
  width: 100%;
}

:root {
  --primary: #fff;
  --outline: rgb(219, 219, 19);
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: #fff;
  background-color: var(--primary);
  color: #242424;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
  display: inline-block;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.btn--outline:hover {
  border: 1px solid rgb(219, 219, 19);
  border: 1px solid var(--outline);
}
.btn--outline::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(219, 219, 19);
  background-color: var(--outline);
  z-index: -1;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all 0.7s;
}
.btn--outline:hover::before {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 15px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  transition: 0.8s;
}

.btn--test {
  padding: 12px 26px;
  font-size: 20px;
  border: 1px solid #000;
}

.btn--test:hover {
  background-color: #000;
  color: rgb(255, 255, 255);
  transition: all 0.9s ease-out;
}

.navbar {
  background: rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 30px 0 rgb(0 0 0 / 30%);
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
  overflow: hidden;
}
.navbar.active {
  background: #000;
  box-shadow: 0 0 50px 0 rgb(0 0 0 / 50%);
  transition: 0.5s ease;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  letter-spacing: 3px;
  font-family: "Jost", sans-serif;
}
.navbar-logo span {
  color: yellow;
  background: #000;
  padding-left: 3px;
  font-weight: 600;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 50vw;
  justify-content: end;
  margin-right: 1.5rem;
}

.nav-item {
  height: 70px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-size: 14px;
}
.nav-links-active {
  border-bottom: 4px solid yellow;
}

.nav-links:hover {
  color: yellow;
  transition: all 0.3s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

.language {
  position: relative;
  left: 40px;
}

@media all and (max-width: 1100px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 80%;
    height: 100%;
    position: fixed;
    top: 70px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-item {
    position: relative;
    top: -150px;
  }

  .nav-links {
    text-align: center;
    font-size: 15px;
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-bottom: none;
    border-radius: 0;
  }
  .nav-links-active {
    border-bottom: none;
    color: yellow;
  }

  .navbar-logo {
    position: absolute;
    top: -10px;
    left: 0;
    margin-left: -15px;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    position: absolute;
    top: 5px;
    right: 30px;
    font-size: 1.8rem;
    cursor: pointer;
    height: 60px;
    width: 60px;
    border: 3px solid white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
    cursor: pointer;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    margin-right: -20px;
  }
  .menu-icon::after {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    border-radius: 50%;
    border: 3px solid white;
    -webkit-animation: 1s ease infinite;
            animation: 1s ease infinite;
  }
  .close-menu {
    height: 2px;
    width: 30px;
    position: relative;
    background-color: white;
    z-index: -1;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 1rem auto;
    width: 70%;
    text-decoration: none;
    font-size: 1rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }

  .language {
    display: none;
  }
  .menu-language {
    display: grid;
    position: relative;
    justify-items: center;
    top: -200px;
  }
}

@media screen and (min-width: 1100px) {
  .menu-language {
    display: none;
  }
}

